.events{
  background-color: #f8f8f9;
  padding-bottom: 300px;
  h3{
    color: rgba(21, 21, 37, 0.6);
    font-family: Regular, sans-serif;
    font-weight: 400;
    line-height: 17px;
    text-align: center;
    margin: 0;
    padding: 99px 0;
  }
  &__block{

  }

}
.block{
  background-color: $white;
  &-img{
    max-width: 484px;
    //width: 100%;
  }
  &-description{
    max-width: 415px;
    //width: 100%;
    padding: 43px 48px 0 48px;
    h2{
      font-family: "Bold", sans-serif;
      line-height: 32px;
    }
    p{
      margin: 19px 0 26px 0;
    }
  }
  &-date{
    max-width: 320px;
    width: 100%;
    border-left: 1px solid #e6e8e9;
    padding: 75px 0px 0 45px;
    span{
      color: $text-color;
      font-family: Regular, sans-serif;
      font-size: 18px;
      line-height: 44px;
      width: 100%;
      display: block;
      padding-left: 51px;
      background-image: url("../images/sp-date.png");
      &:nth-of-type(1){
        background-position: 0 10px;
      }

      background-repeat: no-repeat;
      &:nth-of-type(2){
        background-position: 0 center;
      }
      &:nth-of-type(3){
        background-position: 0 109%;
        margin-bottom: 44px;
      }
    }
  }
  &-calendar{
    position: relative;
    &:hover{
      .select-calendar{
        display: block;
      }
    }
    .btn{
      //margin-top: 44px;

    }
  }
  &-social{
    max-width: 135px;
    width: 100%;
    a{
      padding: 16px;
      background-image: url("../images/social.png");
      background-repeat: no-repeat;
    }
    .face-book{
      background-position: center 0;
    }
    .twitter{
      background-position:  100% 0;
    }
  }
}
.select-calendar{
  position: absolute;
  display: none;
  width: 220px;
  height: 210px;
  background-color: white;
  border: 1px solid #e6e8e9;
  border-radius: 3px;
  a{
    font-size: 18px;
    line-height: 50px;
    color: #666;
    font-family: "Regular", sans-serif;
    background-position: left center;
    background-repeat: no-repeat;
    padding-left: 39px;
    padding-top: 5px;
    padding-bottom: 5px;
    @include transition();
    &:hover{
      color: black;
      font-family: "Black", sans-serif;
      font-weight: 900;
    }
  }
  li{
    padding-left: 11px;
    &:nth-of-type(1) a{
      background-image: url("../images/google.png");
      &:hover{
        background-image: url("../images/google-a.png");
      }
    }
    &:nth-of-type(2) a{
      background-image: url("../images/aple.png");
      &:hover{
        background-image: url("../images/aple-a.png");
      }
    }
    &:nth-of-type(3) a{
      background-image: url("../images/outlook.png");
      &:hover{
        background-image: url("../images/outlook-a.png");
      }
    }
    &:nth-of-type(4) a{
      background-image: url("../images/calendar.png");
      &:hover{
        background-image: url("../images/calendar-a.png");
      }
    }

  }
}