// Все элементы формы

input[type='text'],
input[type='email'],
input[type='tel'],
input[type='url'],
input[type='search'],
input[type='password'],
textarea,
select {
  width: 100%;
  max-width: 100%;
  background: none;
  outline: 0 none;
  padding: 15px;
  color: #333;
  background-color: #fff;
  margin-top: 8px;
  margin-bottom: 12px;
  height: 50px;
  border: 1px solid #e1e1e1;
  font-family: Regular, sans-serif;
  font-size: 18px;
  font-weight: 400;
  line-height: 25px;
}
