// Кнопки

.btn {
  display: inline-block;
  vertical-align: middle;
  background: none;
  outline: 0 none;
  padding: 8px 25px;
  background-color: #000;
  color: #fff;
  font-family: "Medium", sans-serif;
  font-size: 18px;
  font-weight: 500;
  line-height: 28px;
  width: 220px;
  height: 50px;
  border: 2px solid #151525;
  border-radius: 3px;
  &:hover{
    color: #000;
    background-color: $white;
  }
}
