.header {
  position: relative;
  h1{
    font-weight: 900;
    line-height: 104px;
    text-align: center;
    margin: 89px 0;
  }
}
.menu-wrap{
  max-width: 736px;
  margin: 0 auto;
}
.menu{
  padding: 11px 0 27px 0;
}
.navigation, .filter{
  a, .date, .search{
    color: $text-color;
    font-family: "Regular", sans-serif;
    font-size: 20px;
    font-weight: 400;
    line-height: 17px;
    position: relative;
    @include transition();
    &:after{
      content: "";
      position: absolute;
      bottom: -25px;
      left: 0;
      right: 0;
      width: 0;
      margin: 0 auto;
      height: 4px;
      border-bottom: 4px solid #487fc0;
      @include transition();
    }
    &:hover{
      //font-family: Bold, sans-serif;
      font-weight: 900;
      color: #487fc0;
      &:after{
        content: "";
        width: 100%;
        border-bottom: 4px solid #487fc0;
      }
    }
  }
  li{
    width: 25%;
    text-align: center;
  }
}
.navigation{
  max-width: 530px;
  width: 100%;
}
.filter{
  max-width: 140px;
  width: 100%;
  .date, .search{
    position: relative;
    &:after{
      bottom: -27px;
    }
    &:hover{
      a{
        font-weight: 900;
        color: #487fc0;
      }
      .form{
        display: block;
      }
    }
  }
  .search{
    &:hover{
      a{
        background: url("../images/search-a.png") center no-repeat;
      }
    }
    .form{
      left: -128px;
      input{
        max-width: 100%;
      }
    }
  }
}
.form{
  display: none;
  position: absolute;
  left: 0;
  top: 48px;
  width: 100%;
  z-index: 2;
  &:before{
    content: '';
    position: absolute;
    top: -30px;
    left: 0;
    width: 350px;
    height: 30px;
  }
  input{
    width: 100%;
    max-width: 140px;
  }
  form{
    width: 350px;
    padding: 30px;
    background-color: $white;
  }
  .btn{
    width: 100%;
  }
}
.search a{
  background: url("../images/search.png") center no-repeat;
  padding: 10px 16px;
}